.table-wrapper{padding-bottom:104px}.table{width:100%;border-collapse:collapse}.table__button{text-align:center;transition:.4s;font-size:14px;font-weight:600;display:block;padding:3px 16px 5px;text-decoration:none;color:#06c;line-height:32px;white-space:nowrap;background:rgba(0,102,204,.1);border-radius:4px}.table__button:hover{transition:.4s;color:#fff;background:#06c}.table__cell-price{display:flex;justify-content:space-between}.table__cell-length::after{content:"мм";margin-left:2px}.table__cell-rub::after{content:"₽";margin-left:2px}.table th{text-align:left;font-weight:bold;padding:10px 20px;padding-left:30px;background:#efefef}.table tr{border-bottom:1px solid #ddd}.table td{padding:16px 20px 16px 28px}@media(max-width: 600px){.table-wrapper{padding-bottom:60px}.table td{padding:0px}.table__cell-flex{display:flex;flex-grow:1;align-items:center}.table__cell-flex_name{padding-left:10px}.table__cell-price{display:flex;align-content:center;align-items:center;justify-content:space-between}.table__cell-length{padding-left:10px}.table__cell-rub{position:absolute;left:122px}table{border:0}table thead{display:none}table tr{margin-bottom:16px;display:block;border-bottom:2px solid #ddd}table tr .button{margin:0px}table td{display:block;text-align:right;font-size:13px;border:0}table td::before{flex-grow:0;background-color:#f5f5f7;width:80px;text-align:left;content:attr(data-label);float:left;padding:10px;padding-left:10px;font-weight:bold}}